import clientPromise from ".";

var client;
var db;
var users;

async function init() {
    if (db) return;

    try {
        client = await clientPromise;
        db = await client.db();
        users = await db.collection("users");

    } catch(error) {
        throw new Error("Fail to establish a connection to database");

    }
}

(async () => { await init(); })

export async function getUsers() {
    try {
        if (!users)  await init();

        const result = await users
            .find({})
            .limit(10)
            .map((user) => { return { ...user, _id: user._id.toString() }; })
            .toArray();

        return { users : result }
        
    } catch(error) {
        return { error : "Failed to fecth data" + error };

    }
}